// summernote
jQuery(document).ready(function () {
  $('#list-tags').selectize({
    plugins: ['remove_button'],
    delimiter: ',',
    persist: false,
    create: function (input) {
      return {
        value: input,
        text: input
      }
    }
  })
  $('#summernote').summernote({
    placeholder: 'Type here',
    tabsize: 2,
    height: 80
  })
})
